export class ConvertHelper {
    static weight(baseUnit, targetUnit, value) {
        const myBaseUnit = baseUnit.toLowerCase();
        const myTargetUnit = targetUnit.toLowerCase();
        if (myBaseUnit == myTargetUnit) {
            return value;
        }
        else {
            if (myBaseUnit == 'kg') {
                if (myTargetUnit == 'g') {
                    return value / 1000;
                }
                else {
                    if (myTargetUnit == 'mg') {
                        return value / (1000 * 1000);
                    }
                }
            }
            else {
                if (myBaseUnit == 'g') {
                    if (myTargetUnit == 'mg') {
                        return value * 1000;
                    }
                    else {
                        if (myTargetUnit == 'kg') {
                            return value / 1000;
                        }
                    }
                }
            }
        }
    }
}
